<template>
  <v-app>
    <div v-if="$store.state.Authorization === true">
      <default-bar />
      <default-drawer />
      <default-view />
      <default-footer />
    </div>
    <div v-else>
      <default-login />
    </div>
    <!-- <default-settings /> -->
  </v-app>
</template>

<script>
  import axios from 'axios'
  import { get } from 'vuex-pathify'
  export default {
    name: 'DefaultLayout',

    components: {
      DefaultBar: () =>
    import(
      /* webpackChunkName: "default-app-bar" */
      './AppBar'
    ),
      DefaultDrawer: () =>
    import(
      /* webpackChunkName: "default-drawer" */
      './Drawer'
    ),
      DefaultFooter: () =>
    import(
      /* webpackChunkName: "default-footer" */
      './Footer'
    ),
      // DefaultSettings: () => import(
      //   /* webpackChunkName: "default-settings" */
      //   './Settings'
      // ),
      DefaultView: () =>
    import(
      /* webpackChunkName: "default-view" */
      './View'
    ),
      DefaultLogin: () =>
    import(
      /* webpackChunkName: "default-view" */
      '../../views/Login.vue'
    ),
    },

    computed: {
      ...get(['user', 'authentication', 'Authorization']),
    },

    mounted () {
      this.checkAuth()
    },

    methods: {
      checkAuth () {
        axios
          .get('auth/user')
          .then(() => {
            console.log('ok')
          })
          .catch(() => {
            axios.get('auth/refresh').then((res) => {
              axios.defaults.headers.common.Authorization =
                'Bearer ' + res.data.token

              this.$store.commit('authentication', res.data.token)
              this.$store.commit('Authorization', true)
              this.$store.commit('users', res.data.users)
            })
          })
      },
    },

  }

</script>
